<script>
import anime from 'animejs'

export default {
  name: 'SvgTitle',
  mounted () {
    this.animation()
  },
  methods: {
    animation () {
      anime({
        targets: '#img-title path',
        strokeDashoffset: [anime.setDashoffset, 0],
        easing: 'easeInOutQuad',
        duration: 500,
        delay: function (el, i) { return i * 10 },
        direction: 'alternate',
        loop: false
      })
    }
  }
}
</script>

<style scoped>
@media (max-width: 767px) {
  svg#img-title {
    height: 123px;
    width: 331px;
  }
}
@media (min-width: 768px) {
  svg#img-title {
    height: 211px;
    width: 570px;
  }
}

</style>



